.side-drawer{
    position: absolute;
    top: 0;
    left: 0;
    width: 26%;
    height: 100vh;
    background-color: #D8DBE3;
    z-index: 100;
    border-top-right-radius: 0.4rem;
    border-bottom-right-radius: 0.4rem;
    /* transform: translateX(-100%); */
    transition: transform 0.6s ease-out;
}
.drawer-header{
    font-size: 1.8rem;
    display:flex;
    justify-content: space-around;
    align-items: center;
    height: 8vh;
    padding-top:0.2rem;
}
.drawer-body{
    height: 92vh;
    padding: 0.6rem;
    padding-top: 1.5rem;
}

.drawer-searchbox{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.chatlist{
    margin-top:5%;
    height:90%;
    overflow-y: scroll;
    background-color: #fff;
    padding-top:0.6rem;
    border-radius: 1rem;
    -ms-overflow-style: none; 
    scrollbar-width: none;
}

.chatlist::-webkit-scrollbar { 
    /* Safari and Chrome */
    display: none;  
} 

@media screen and (max-width: 1200px) {
    .side-drawer{
        width: 40%;
    }
  }

@media screen and (max-width: 900px) {
    .side-drawer{
        width: 50%;
    }
  }

  @media screen and (max-width: 650px) {
    .side-drawer{
        width: 70%;
    }
  }

  @media screen and (max-width: 450px) {
    .side-drawer{
        width: 85%;
    }
  }